import { Fragment, useLayoutEffect, useRef } from "react";
import { reqLogin } from "./api";
import { useDispatch, useSelector } from "../../app/hooks";
import { history } from "../../index";
import { selectIsLogin, selectMsg, selectUser, setMsg } from "./slice";
import {
  Wrap,
  Container,
  Title,
  Content,
  MsgBox,
  UsernameField,
  PasswordField,
  MainButton,
  SwitchField,
} from "./styles";

export function LoginPage() {
  const user = useSelector(selectUser);
  const isLogin = useSelector(selectIsLogin);
  const msg = useSelector(selectMsg);
  const usernameInput = useRef<HTMLInputElement>(null);
  const passwordInput = useRef<HTMLInputElement>(null);
  const dispatch = useDispatch();
  
  const routeToRegister = () => {
    dispatch(setMsg("")) //clear
    history.push({ pathname: "/register" });
  };

  const onLogin = () => {
    const username = usernameInput.current?.value + "";
    const password = passwordInput.current?.value + "";

    if (username === "") {
      // console.log("");
      dispatch(setMsg(""));
      return;
    }
    if (password === "") {
      // console.log("");
      dispatch(setMsg(""));
      return;
    }

    dispatch(reqLogin(username, password));
  };

  // logout
  useLayoutEffect(()=>{
    if (isLogin){
      dispatch(setMsg(`${user.username} ?`))
      history.push({ pathname: "/logout" });
    }
    return ()=>{}
  },[])
  
  useLayoutEffect(() => {
    const {username} = user
    if (usernameInput.current && username !== "") {
      usernameInput.current.value = username;
    }
  }, [usernameInput, user]);


  return (
    <Wrap>
      <Container>
        <Title>
          <h1></h1>
        </Title>
        <Content>
          {msg !== "" ? (
            <MsgBox>
              <h1>{msg}</h1>
            </MsgBox>
          ) : (
            <Fragment />
          )}
          <UsernameField>
            <input
              type="text"
              // placeholder="?"
              ref={usernameInput}
              required
            />
            <span />
            <label>{""}</label>
          </UsernameField>
          <PasswordField>
            <input
              type="password"
              // placeholder="?"
              ref={passwordInput}
              required
            />
            <span />
            <label>{""}</label>
          </PasswordField>
          <MainButton onClick={onLogin}>
            <p> </p>
          </MainButton>{" "}
          <SwitchField>
            <p>?</p>
            <div onClick={routeToRegister}></div>
          </SwitchField>
        </Content>
      </Container>
    </Wrap>
  );
}
