import { useLayoutEffect } from "react";
import { useRef } from "react";
import { useDispatch } from "react-redux";
import { reqRegister } from "./api";
import { history } from "../../index";
import { useSelector } from "../../app/hooks";
import { selectIsLogin, selectMsg, selectUser, setMsg } from "./slice";
import {
  Wrap,
  Container,
  Title,
  Content,
  MsgBox,
  UsernameField,
  PasswordField,
  MainButton,
  SwitchField,
} from "./styles";
import { Fragment } from "react";

export function RegisterPage() {
  const user = useSelector(selectUser);
  const isLogin = useSelector(selectIsLogin);
  const msg = useSelector(selectMsg);
  const usernameInput = useRef<HTMLInputElement>(null);
  const passwordInput = useRef<HTMLInputElement>(null);
  const passwordInput2 = useRef<HTMLInputElement>(null);
  const dispatch = useDispatch();

  const routeToLogin = () => {
    dispatch(setMsg("")); // clear
    history.push({ pathname: "/login" });
  };

  const onRegister = () => {
    const username = usernameInput.current?.value + "";
    const password = passwordInput.current?.value + "";
    const password2 = passwordInput2.current?.value + "";

    if (username === "") {
      // console.log("");
      dispatch(setMsg(""));
      return;
    }
    if (password === "") {
      // console.log("");
      dispatch(setMsg(""));
      return;
    }
    if (password !== password2) {
      // console.log("");
      dispatch(setMsg(""));
      return;
    }

    dispatch(reqRegister(username, password));
  };

  // logout
  useLayoutEffect(()=>{
    if (isLogin){
      dispatch(setMsg(`${user.username} ?`))
      history.push({ pathname: "/logout" });
    }
    return ()=>{}
  },[])

  useLayoutEffect(() => {
    const {username} = user;
    if(usernameInput.current && username !== "") {
      usernameInput.current.value = username;
    }
  }, [usernameInput, user])

  return (
    <Wrap>
      <Container>
        <Title>
          <h1></h1>
        </Title>
        <Content>
          {msg !== "" ? (
            <MsgBox>
              <h1>{msg}</h1>
            </MsgBox>
          ) : (
            <Fragment/>
          )}
          <UsernameField>
            <input type="text" ref={usernameInput} required />
            <span />
            <label>{""}</label>
          </UsernameField>
          <PasswordField>
            <input type="password" ref={passwordInput} required />
            <span />
            <label>{""}</label>
          </PasswordField>
          <PasswordField>
            <input type="password" ref={passwordInput2} required />
            <span />
            <label>{""}</label>
          </PasswordField>
          <MainButton onClick={onRegister}>
            <p> </p>
          </MainButton>{" "}
          <SwitchField>
            <p>?</p>
            <div onClick={routeToLogin}></div>
          </SwitchField>
        </Content>
      </Container>
    </Wrap>
  );
}
